.slider-wrapper {
  width: 100%;
  overflow-x: scroll;
  padding: 100px 0 30px 0;
  flex-direction: row;
  border-bottom: 1px solid rgb(229, 229, 229);
  &::-webkit-scrollbar {
    display: none;
  }
  -ms-overflow-style: none;
  scrollbar-width: none;
  .SliderWrapper-Inner {
    width: fit-content;
    display: flex;
    grid-column-gap: 15px;
    padding: 0 15px;
    .Slide {
      width: 140px;
      height: 200px;
      position: relative;
      .Slide-Bg {
        width: 100%;
        height: 100%;
        background: linear-gradient(
          rgba(18, 18, 18, 0),
          rgba(18, 18, 18, 0),
          rgba(18, 18, 18, 0.4)
        );
        position: absolute;
        top: 0;
        left: 0;
        z-index: 9;
      }
      .Slide-Img {
        width: 100%;
        height: 100%;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      .Slide-Title h2 {
        position: absolute;
        bottom: 15px;
        left: 15px;
        z-index: 10;
        margin: 0;
        font-size: 1rem;
        color: #fff;
      }
    }
  }
}
